<template>
  <header class="header">
    <div class="header__left">
      <img src="../images/ojovo.svg" alt="ojovo">
      <p class="header__beta">Beta</p>
    </div>
    <form class="header__right">
      <button type="button" class="header__bell">
        <img src="../images/bell.svg" alt="bell">
      </button>
      <button type="button" class="header__select-name name" v-on:click="showName">
        {{name}}
        <img src="../images/arrow-bottom.svg" alt="select" class="arrow name">
        <div class="list name">
          <button type="button" v-on:click="e => changeName(e)">Артем</button>
          <button type="button" v-on:click="e => changeName(e)">Василий</button>
        </div>
      </button>
      <button type="button" class="header__search">
        <img src="../images/search.svg" alt="bell" />
      </button>
      <button 
        type="button" 
        class="header__select-language language-include" 
        v-on:click="showLanguage"
      >
        {{language}}
        <img src="../images/arrow-bottom.svg" alt="select" class="language-include">
        <div class="language language-include" >
          <button type="button" v-on:click="e => changeLanguage(e)">Ru</button>
          <button type="button" v-on:click="e => changeLanguage(e)">En</button>
        </div>
      </button>
    </form>
  </header>
</template>
<script>
export default {
  name: "Header",
  data: function() {
    return {
      name: 'Артем',
      language: 'Ru'
    }
  },
  methods: {
    changeName: function (e) {
      this.name = e.target.innerText;
    },
    changeLanguage: function (e) {
      this.language = e.target.innerText;
    },
    showName: function () {
      document.querySelector('.list').style.display = 'flex';
    },

    showLanguage: function () {
      document.querySelector('.language').style.display = 'flex';
    },
  }
};
</script>

<style scoped>
.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;
}

.header__left {
  margin-top: 26px;
  display: flex;
  align-items: baseline;
}

.header__beta {
  margin-left: 9px;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 140%;
  margin-top: 0;
  margin-bottom: 0;
}

.header__right {
  display: flex;
  margin-top: 10px;
}

.header__bell {
  background-color: white;
  border-radius: 50%;
  width: 43px;
  height: 43px;
  margin-right: 17px;
}

.header__bell:hover {
  cursor: pointer;
}

.header__search {
  background-color: rgba(203, 215, 228, 0.4);
  border-radius: 50%;
  width: 43px;
  height: 43px;
  margin-right: 17px;
}

.header__search:hover {
  cursor: pointer;
}

.header__select-name {
  background: #ffffff;
  border-radius: 100px;
  width: 120px;
  height: 43px;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  margin-right: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.header__select-name:hover {
  cursor: pointer;
}

.header__select-language {
  background: rgba(203, 215, 228, 0.4);
  border-radius: 100px;
  width: 64px;
  height: 43px;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  margin-right: 17px;
  display: flex;
  justify-content: center;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.header__select-language:hover {
  cursor: pointer;
}

.header__select-language select {
  margin: 0;
  padding: 0;
  height: 100%;
  border-radius: 100px;
  background: rgba(203, 215, 228, 0);
}

.header__select-language select:hover {
  cursor: pointer;
}

.header__select-name {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.list, .language {
  position: absolute;
  bottom: -34px;
  left: 16px;
  display: none;
  flex-direction: column;
  align-items: flex-start;
  text-align: start;
}

.list button, .language button {
  background: white;
  width: 100%;
  border: 0;
  outline: 0;
  text-align: start;
}

.list button:hover, .language button:hover {
  cursor: pointer;
  background: lightblue;
}

</style>